.header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 8rem 1fr;
}
.header__information {
  max-width: 50rem;
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  padding-top: 15rem;
  align-items: center;
  position: relative;
}
.header__information .blurred-ball--green {
  position: absolute;
  top: -10%;
  left: 50%;
}
.header__information .blurred-ball--blue {
  position: absolute;
  top: 10%;
  left: -10%;
}
.header__information__title span {
  color: var(--primary);
}
.header__information p {
  font-weight: 200;
  color: var(--grey3);
}
.header__image {
  grid-column: 2/3;
  grid-row: 1/3;
}

.pollyfill {
  display: none;
}

.navbar {
  grid-row: 1/2;
  grid-column: 1/3;
}

/*
0 - 600px   Phone
600 - 900px     Tablet portrait
900 - 1200px    Tablet landscape
1200 - 1800px   Normal styles applied
1800px+    Big Desktop
*/
.navbar {
  width: 100%;
  color: #000;
  opacity: 0.85;
  height: 8rem;
  z-index: 12;
}
.navbar input[type=checkbox],
.navbar .hamburger__lines {
  display: none;
}
.navbar__container {
  display: flex;
  justify-content: space-between;
  padding: 2rem 12rem;
  align-items: center;
}
.navbar__container .menu__items {
  order: 2;
  display: flex;
  gap: 1.5rem;
}
.navbar__container .menu__items li {
  list-style: none;
}
.navbar__container .menu__items a {
  transition: color 0.3s ease-in-out;
}
.navbar__container .menu__items a:hover {
  transition: color 0.3s ease-in-out;
}
.navbar__container .logo {
  order: 1;
  margin-bottom: 0.5rem;
}
@media (max-width: 52.25em) {
  .navbar {
    position: fixed;
    opacity: 0.95;
  }
  .navbar input[type=checkbox],
  .navbar .hamburger__lines {
    display: block;
  }
  .navbar__container {
    display: block;
    position: relative;
    height: 64px;
    padding: 0;
  }
  .navbar__container input[type=checkbox] {
    display: block;
    position: absolute;
    height: 3rem;
    width: 3rem;
    top: 2rem;
    left: 2rem;
    z-index: 5;
    opacity: 0;
  }
  .navbar__container .hamburger__lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 3rem;
    width: 3.5rem;
    position: absolute;
    top: 2.5rem;
    left: 2rem;
    z-index: 2;
  }
  .navbar__container .hamburger__lines .line {
    display: block;
    height: 3px;
    width: 100%;
    background: var(--black);
  }
  .navbar__container .hamburger__lines .line.line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  .navbar__container .hamburger__lines .line.line2 {
    transition: transform 0.2s ease-in-out;
  }
  .navbar__container .hamburger__lines .line.line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
  .navbar .menu__items {
    padding-top: 100px;
    background: rgba(255, 255, 255, 0.5);
    height: 100vh;
    max-width: 300px;
    transform: translate(-150%);
    flex-direction: column;
    margin-left: -40px;
    padding-left: 50px;
    box-shadow: 0.5rem 0px 2rem 1rem rgba(226, 226, 226, 0.5882352941);
  }
  .navbar .menu__items li {
    margin-bottom: 1.5rem;
    font-size: 1.3rem;
    font-weight: 500;
  }
  .navbar .logo {
    position: absolute;
    top: 2.5rem;
    right: 2rem;
    font-size: 2rem;
  }
  .navbar input[type=checkbox]:checked ~ .menu__items {
    transform: translateX(0);
    transition: transform 0.5s ease-in-out;
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line1 {
    transform: rotate(50deg);
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line2 {
    transform: scaleY(0);
  }
  .navbar input[type=checkbox]:checked ~ .hamburger__lines .line3 {
    transform: rotate(-50deg);
  }
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  background-color: var(--white);
  font-family: var(--font-default);
}

body p {
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 300;
}

.text-body-medium {
  font-weight: 500;
}

.text-body-bold {
  font-weight: 600;
}

h1,
h2,
h3,
h4 {
  text-transform: capitalize;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.2rem;
  font-weight: 600;
}

h3 {
  font-size: 2.4rem;
  font-weight: 500;
}

h4 {
  font-size: 2rem;
  font-weight: 500;
}

.subtitle {
  font-size: 1.8rem;
  font-weight: 500;
}

.label {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: var(--white);
}
.label--small {
  font-size: 1.2rem;
}
.label--medium {
  font-weight: 500;
}
.label--bold {
  font-weight: 600;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --white: #fff;
  --transparent-white: rgba(255, 255, 255, 0.1);
  --grey1: #e0e3eb;
  --grey2: #888b97;
  --grey3: #626687;
  --black: #1b1c57;
  --primary: #10b981;
  --primary-dark: #0b9567;
  --primary-light: #d1fae5;
  --secondary: #f59e0b;
  --danger: #ef4444;
  --danger-light: #fee2e2;
  --info: #1d4ed8;
  --info-light: #dbeafe;
  --green-blur: #82ffd1c4;
  --blue-blur: #82f7ffe1;
}

:root {
  --font-default: "Lexend", sans-serif;
}

@keyframes moveNavInLeft {
  0% {
    opacity: 0;
    transform: translate(-10rem, -50%);
  }
  80% {
    transform: translate(1rem, -50%);
  }
  100% {
    opacity: 1;
    transform: translate(0, -50%);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.label__container {
  padding: 0.8rem 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
}
.label--danger {
  color: var(--danger);
  background-color: var(--danger-light);
}
.label--info {
  color: var(--info);
  background-color: var(--info-light);
}
.label--primary {
  color: var(--primary-dark);
  background-color: var(--primary-light);
}

.button {
  color: var(--white);
}
.button, .button:link, .button:visited {
  text-transform: capitalize;
  text-decoration: none;
  gap: 1.5rem;
  border-radius: 10rem;
  border: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
.button--white {
  height: 3.4rem;
  padding: 0.8px 1.6rem;
  border: 1px solid var(--white) !important;
  background-color: var(--transparent-white);
}
.button--white:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.blurred-ball, .blurred-ball--blue, .blurred-ball--green {
  width: 35rem;
  height: 35rem;
  border-radius: 50%;
  filter: blur(120px);
  z-index: -1;
}
.blurred-ball--green {
  background-color: var(--green-blur);
}
.blurred-ball--blue {
  background-color: var(--blue-blur);
}